.LoadingContainer{
    width: 500px;
    height:600px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    background-color: blanchedalmond;
    border-radius: 5px;

}
.loader{
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    padding: 10px;
    background-color: rgba(2,0,36,0.5);
    background: linear-gradient(
        46deg,
        rgba(2,0,36,1)0%
        rgba(9,9,121,1)35%
        rgba(0,212,255,1)100%
    );
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    animation: rotation 0.5 linear infinite;
}
.loader div{
    background-color: aliceblue;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    border-radius: 50%;
}
@keyframes rotation{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate3d(350deg);
    }
}